<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/rolling-banner')" class="current">메인 배너</li>
			<li @click="goto('/admin/introduction')">운동친구 소개</li>
			<li @click="goto('/admin/admin-member')">운동친구 멤버</li>
			<li @click="goto('/admin/faq')">자주묻는질문</li>
			<li @click="goto('/admin/terms')">약관</li>
		</ul>

		<div class="contents-container">
			<p class="contents-title underline marB-20 body3">배너 등록</p>
			<div class="input-container">
				<div class="input-item align-top">
					<AddImageSingle
						:refName="'bannerImage'"
						@bannerImage="setMainImage"
					/>
				</div>

				<div class="input-item">
					<p class="small-title">카테고리</p>
					<div class="select-box">
						<SelectBoxAdmin
							class="small marB-20"
							:options="options"
							v-model="selected"
							placeholder="카테고리를 선택해주세요"
						/>
					</div>
				</div>

				<div class="input-item">
					<p class="small-title">제목</p>
					<input type="text" class="admin-input large" v-model="title" />
				</div>

				<div class="input-item ">
					<p class="small-title">링크</p>
					<input
						type="text"
						class="admin-input medium marR-20"
						v-model="link"
					/>
				</div>

				<div class="input-item">
					<p class="small-title">버튼 내용</p>
					<input
						type="text"
						class="admin-input medium marB-10"
						placeholder="6글자 이하"
						v-model="btnTitle"
					/>
				</div>
				<button class="outlined-btn" @click="addBanner">
					등록
				</button>
			</div>
		</div>
	</div>
</template>

<script>
import AddImageSingle from '@/components/common/AddImageSingle';
import SelectBoxAdmin from '@/components/common/SelectBoxV2';

import { addBanner } from '@/api/banner';
import Store from '@/store';

import { mapGetters } from 'vuex';

export default {
	components: { AddImageSingle, SelectBoxAdmin },
	computed: {
		...mapGetters(['main']),
	},
	created() {
		Store.commit('SET_MAIN', null);
	},
	data() {
		return {
			options: [
				{ id: 'PR', label: '프로그램' },
				{ id: 'GD', label: '굿즈' },
				{ id: 'NT', label: '공지사항' },
				{ id: 'NS', label: '대외뉴스' },
				{ id: 'YT', label: '유튜브' },
				{ id: 'CL', label: '칼럼' },
			],
			selected: {},
			title: '',
			link: '',
			btnTitle: '',
			errMsg: '',
		};
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async addBanner() {
			if (!this.main) {
				this.errMsg = '이미지를 등록해주세요';
				alert(this.errMsg);
				return false;
			}

			if (this.selected == 'NONE') {
				this.errMsg = '카테고리를 선택해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.title) {
				this.errMsg = '제목을 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.link) {
				this.errMsg = '링크를 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.btnTitle) {
				this.errMsg = '버튼명을 입력해주세요';
				alert(this.errMsg);
				return false;
			}

			let data = new FormData();
			data.append('file', this.main);
			data.append('title', this.title);
			data.append('categoryId', this.selected.id);
			data.append('link', this.link);
			data.append('btnTitle', this.btnTitle);
			try {
				await addBanner(data);
				alert('배너가 등록되었습니다');
				await this.initForm();
			} catch (e) {
				alert(e);
			}
		},
		initForm() {
			this.selected = '';
			this.title = '';
			this.link = '';
			this.btnTitle = '';
			Store.commit('SET_MAIN', null);
		},
		setMainImage(data) {
			Store.commit('SET_MAIN', data);
			console.log(this.main);
		},
	},
};
</script>

<style></style>
